        Object.each(qBits, function (val, key) {
            if (key.toLowerCase() !== 'ran') {
                if (key !== 'parameters') {
                    qBitsArray.push(key + '-' + val);
                } else {
                    params = JSON.parse(val);
                }
            }
        });
        qBitsArray = qBitsArray.sort();

