var UIListBuilderWidget = new Class({

    Implements: [Options, Events],

    options: {
        target: null,
        delimeter: ','
    },

    Binds: [
        ''
    ],

    list: [],
    outputList: [],

    initialize: function (options) {

        this.setOptions(options);

        this.icons = {};
        this.icons.del = Affinity.icons.CrossRound;

        this.options.target.addClass('uidone');

        this.select = this.options.target;

        var name = this.select.get('name');
        var id = this.select.get('id');
        var validationMethods = this.select.get('data-validate-method') || '';
        var validateStr = this.select.get('data-validate-error-str') || '';
        this.select.erase('data-validate-method');
        this.select.erase('data-validate-error-str');
        this.select.removeClass('validate');

        this.select.erase('name');

        this.output = new Element('input', { 'type': 'hidden', 'name': name, 'id': id }).inject(this.select, 'before');

        if (validationMethods && validationMethods!='') {
            this.output.addClass('validate');
            this.output.set('data-validate-method', validationMethods);
            if (validateStr != '') {
                this.output.set('data-validate-error-str', validateStr);
            }
        }

        delete name;
        delete id;
        delete validationMethods;
        delete validateStr;

        this.selectButton = new Element('span', { 'class': 'button green w-icon-only ui-list-builder-button' }).adopt(new Element('span', { 'html': Affinity.icons.PlusRound })).inject(this.select, 'after');

        this.listTable = new Element('table', { 'class': 'ui-grid ui-list-builder-grid' }).inject(this.selectButton, 'after');

        new Element('thead').adopt(
            new Element('tr').adopt(
                new Element('th', { 'html': 'Selected' }),
                new Element('th', { 'html': '' })
            )
        ).inject(this.listTable);

        this.lsitTableBody = new Element('tbody').inject(this.listTable);

        this.empty = new Element('tr').adopt(
            new Element('td', { 'colspan': 2, 'html': 'None' })
        ).inject(this.lsitTableBody);

        new Element('div', { 'class': 'section-table ui-list-builder-container' }).wraps(this.listTable);

        Array.each(this.select.getElements('option'), function (option) {

            this.list.push({ value: option.get('value'), display: option.get('html') });

        }.bind(this));

        this.selectButton.addEvent('click', this.addItem.bind(this));

    },

    addItem: function (e) {
        if (!this.outputList.contains(this.select.value)) {
            if(this.outputList.length==0){
                this.lsitTableBody.empty();
            }
            this.outputList.push(this.select.value);
            new Element('tr', { 'data-value': this.select.value }).adopt(
                new Element('td', { 'html': this.list[this.select.selectedIndex].display }),
                new Element('td').adopt(
                    new Element('span', { 'class': 'button orange w-icon-only', 'html': '<span>' + this.icons.del + '</span>' }).addEvent('click', this.removeItem.bind(this))
                )
            ).inject(this.lsitTableBody);
        }
        this.output.value = this.outputList.toString().replace(/,/g, this.options.delimeter);
        if (Affinity.uiGrid) { Affinity.uiGrid.zebra(this.listTable); }
    },

    removeItem: function (e) {
        var row = e.target.getParent('tr');
        var value = row.get('data-value');
        this.outputList.erase(value);
        row.destroy();
        if (this.outputList.length == 0) {
            this.empty.inject(this.lsitTableBody);
        }
        delete row;
        delete value;
        this.output.value = this.outputList.toString().replace(/,/g, this.options.delimeter);
        if (Affinity.uiGrid) { Affinity.uiGrid.zebra(this.listTable); }
    }


});

