var UIPagerWidget = new Class({

    Implements: [Options, Events],

    Binds: [
        'onTableChange',
        'onPrevClick',
        'onNextClick',
        'onPageClick',
        'onGoToClick'
    ],

    options: {
        table: null,
        itemLabel: 'item(s)',
        pageSize: 0,
        maxPages: 15
    },

    initialize: function (options) {
        this.setOptions(options);

        this.table = this.options.table;

        this.element = new Element('div', { 'class': 'pagination' }).store('pager', this);

        var line = new Element('div', { 'class': 'nav-buttons' }).inject(this.element);

        this.prevButton = new Element('span', { 'text': 'previous' })
            .inject(line).addEvent(Affinity.events.click, this.onPrevClick);

        this.pageCtr = new Element('span').inject(line);

        this.nextButton = new Element('span', { 'text': 'next' })
            .inject(line).addEvent(Affinity.events.click, this.onNextClick);;

        this.label = new Element('span', { 'class': 'desc' }).inject(this.element);

        if (typeOf(this.table) !== 'null') {
            this.element.inject(this.table, 'after');
            this.table.addEvent('change', this.onTableChange);
            this.update(0, 0);
        }
    },

    update: function (pageNum, totalRowCount) {
        pageNum = parseInt(pageNum);
        totalRowCount = parseInt(totalRowCount);

        var pageSize = parseInt(this.options.pageSize);
        if (pageSize <= 0) pageSize = NaN;

        var pageCount = !isNaN(totalRowCount) && !isNaN(pageSize) ? Math.floor(totalRowCount / pageSize) : NaN;
        if (totalRowCount % pageSize > 0) pageCount++;

        this.pageCount = pageCount;

        if (!isNaN(pageCount) && !isNaN(pageNum)) {
            if (pageNum < 0) pageNum = 0;
            else if (pageNum >= pageCount) pageNum = pageCount - 1;

            this.currentPage = pageNum;

            this.pageCtr.empty();

            if (pageNum === 0) this.prevButton.addClass('disabled');
            else this.prevButton.removeClass('disabled');

            if (pageNum === pageCount - 1) this.nextButton.addClass('disabled');
            else this.nextButton.removeClass('disabled');

            var start = totalRowCount === 0 ? -1 : pageNum * pageSize;
            var end = start + (pageNum === pageCount - 1 ? totalRowCount % pageSize : pageSize);
            if (end === start) end += pageSize;

            var pages = [];
            if (pageCount > this.options.maxPages) {
                var sectionCount = Math.floor(this.options.maxPages / 3);
                var wingLength = Math.floor(sectionCount / 2);

                var length = this.options.maxPages - sectionCount;
                var appended = pageNum + wingLength < length;

                for (var i = 0; i < (appended ? length : sectionCount) ; i++) {
                    pages.push(i);
                }

                appended = pageNum - wingLength >= pageCount - length;

                for (i = pageCount - 1; i >= (pageCount - (appended ? length : sectionCount)) ; i--) {
                    pages.push(i);
                }

                if (!appended) {
                    for (var i = pageNum - wingLength; i <= pageNum + wingLength; i++) {
                        if (i < 0 || i > pageCount - 1) pages.push(pageNum - i);
                        else pages.push(i);
                    }
                }
            }
            else for (var i = 0; i < pageCount ; i++) {
                pages.push(i);
            }

            pages.sort(function (a, b) { return a - b; });

            var prevNum = -1;
            pages.unique().each(function (num) {
                if (prevNum != num - 1) {
                    var goToButton = new Element('span', { text: '...' }).addEvent(Affinity.events.click, this.onGoToClick);
                    this.pageCtr.adopt(goToButton);
                }

                var button = new Element('span', { 'class': 'page', text: num + 1 })
                    .addEvent(Affinity.events.click, this.onPageClick);

                if (num === this.currentPage) button.addClass('selected');

                prevNum = num;

                this.pageCtr.adopt(button);
            }, this);

            this.label.set('text', 'Showing ' + this.options.itemLabel
                + ' ' + (++start) + ' to ' + end + ' of ' + totalRowCount)

            this.show();
        }
        else this.hide();
    },

    hide: function () {
        this.element.addClass('hidden');
    },

    show: function () {
        this.element.removeClass('hidden');
    },

    onTableChange: function (event) {
        var pageNum = event.page;
        if (typeOf(pageNum) == 'null') pageNum = this.currentPage;
        this.update(pageNum, event.rows);
    },

    onPrevClick: function (event) {
        if (event.target.hasClass('disabled')) return;

        this.table.fireEvent('pageChange', --this.currentPage);
    },

    onNextClick: function () {
        if (event.target.hasClass('disabled')) return;

        this.table.fireEvent('pageChange', ++this.currentPage);
    },

    onGoToClick: function () {
        uiprompt({
            message: 'Go to page:',
            onOk: function (value) {
                var num = parseInt(value) - 1;
                if (isNaN(num)) return;

                if (num < 0) num = 0;
                else if (num >= this.pageCount) num = this.pageCount - 1;

                this.currentPage = num;
                this.table.fireEvent('pageChange', this.currentPage);
            }.bind(this)
        });
    },

    onPageClick: function (event) {
        if (event.target.hasClass('selected')) return;

        this.currentPage = parseInt(event.target.get('text'));
        if (!isNaN(this.currentPage)) this.currentPage--;

        this.table.fireEvent('pageChange', this.currentPage);
    }

});

